:root{
  --photo-size: 85px;
  --light-gray:hsl(0, 0%, 95%);
  --blue: hsl(241, 94%, 56%);
}

/* styles */
body{
  background-color: var(--blue) ;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 100 auto;
  padding: 20px;
  color:rgb(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50% , -50%);
  padding: 10px;
  padding-left: 20px;
  display: grid;
  border-radius: 10px;
  text-align: center;
  height: 400px;
  width: 300px;
  background-color: rgb(250, 250, 250);
}

img{
  margin: auto;
  width: var(--photo-size);
  height: var(--photo-size);
  border-radius: 50px;
  padding: 5px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50% , -150%);
}

h1{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50% , -130%);
}

/* Buttons and Links */
.tech{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50% , 20%); 
}

.btns button{
  height: fit-content;
  width: fit-content;
  background-color: var(--light-gray);
  color: var(--blue);
  border: none;
  border-radius: 5px;
  
}

.name-button:hover, .skill-button:hover, .likes:hover{
  background-color: var(--blue);
  color: white;
}

.name-button{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-110% , 400%);
}

.skill-button{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(20% , 400%);
}

.likes img{
  background-color: var(--blue);
  color: var(--blue);
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50% , 500%);
}
.likes{
  height: fit-content;
  width: fit-content;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50% , 500%);
}

.linkedIn img{
  width: 20px;
  height: 20px;
  transform: translate(-50% , 500%);
}

.linkedIn{
  height: fit-content;
  width: fit-content;
  position: fixed;
  left: 50%;
  top: 50%;

}

@media (orientation: landscape) and (max-width: 800px){
  .container{
    width: 400px;
    height: 250px;
  }
  
  img{
    transform: translate(-50% , -140%);
  }
  
  .name-button{ 
    transform: translate(-110% , 300%);
  }
  
  .skill-button{
    transform: translate(20% , 300%);
  }

  .likes img{
    transform: translate(-50% , 400%);
  }

  .likes{
    transform: translate(-50% , 425%);
  }
  .linkedIn img{
    transform: translate(500% , 300%);
  }

}
